:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}

.blog{
    .mainContainer{
        gap: 1rem;
        .singlePost{
            gap: 2rem;

            .imgDiv{
                height: 200px;
                width: 100%;
                border-radius: 1rem;
                border: 5px solid var(--greybgclr);
                box-shadow: 0 2px 8px 2px rgba(178,178,178,0.406);
                overflow: hidden;

                &:hover{
                    img{
                        transform: scale(1.1);
                        transition: 3s ease;
                    }
                }
                img{
                    height: 100%;
                    object-fit: cover;
                }
            }
            .postDetails{
                h3{
                    color: var(--blackclr);
                    font-weight: 600;
                    padding-bottom: 10px;
                }
                p{
                    font-size: 13px;
                    color: var(--textclr);
                    font-weight: 500;
                }
                
            }
            a{
                font-size: 15px;
                color: var(--primaryclr);
                font-weight: 600;
                padding-top: .5rem;
                width: max-content;
                &:hover{
                    color: var(--hoverclr);
                    font-weight: 600;
                    .icon{
                        transform: translateX(10px);
                    }
                }
            }
        }
    }
}

// responsive
@media screen and (min-width:884px){
   .blog{
    .mainContainer{
        grid-template-columns: repeat(2,1fr);
    }
   }
}