:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}

.footer{
    background:var(--greybgclr) ;
    padding: 2rem 0;

    .secContainer{
        gap:1.5rem;
        text-align: center;
        .footerLogo{
            padding-bottom: .5rem;
            .logo{
                color: var(--blackclr);
                font-weight: 500;
                cursor: pointer;
                align-items: center;
                justify-content: center;

                .icon{
                    font-size: 25px;
                    color: var(--primaryclr);
                    margin-right: .5rem;

                }
            }
        }
        .socials{
            gap: .5rem;
            margin: auto;
            justify-content: center;

            .icon{
                font-size: 30px;
                background: var(--primaryclr);
                border-radius: 50%;
                padding: 8px;
                color: var(--whiteclr);
                &:hover{
                    background: var(--blackclr) ;
                }
            }

        }
        .footerLinks{
            display: grid;
            row-gap: .2rem;

            .linkTitle{
                padding-bottom: .5rem;
                color: var(--blackclr);
                font-weight: 700;

            }
             li{
                &:hover{
                    transform: translateX(10px);
                    a{
                        color: var(--primaryclr);
                    }
                }
             }
             a,.phone,.email{
                color: var(--textclr);
                font-size: 13px;
                font-weight: 500;

             }

        }
    }
}

@media screen and (min-width:440px){
    .secContainer{
        grid-template-columns: repeat(2,1fr);
        text-align: center;
        align-items: flex-start;

        .footerLinks{
            justify-content: center;
        }
    }
}
@media screen and (min-width:640px){
    .secContainer{
        grid-template-columns: repeat(4,1fr);
        
    }
}
@media screen and (min-width:900px){
    .secContainer{
      padding: 2rem;
        
    }
}