@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: .3s ease;
}
:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}
.section{
    padding: 4rem 0 2rem;
}
.container{
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}
.icon{
    font-size: 2rem;
    cursor: pointer;
}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}
.btn{
    padding: .4rem 1.5rem;
    background: var(--primaryclr);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
}
.btn a{
    color: var(--whiteclr);
    font-weight: 500;
}
.btn:hover{
    color: var(--hoverclr);
}
.btn:hover a{
    color: var(--whiteclr);
}
.secIntro{
    max-width: 70%;
    text-align: center;
    margin: 0 auto 2rem;
}
.secIntro .secTitle{
    color: var(--blackclr);
    line-height: 1.5rem;
    padding-bottom: .5rem;
}
.secIntro p{
    font-size: 13px;
    color: var(--textclr);
    font-weight: 500;
}
img,video{
    width: 100%;
    height: auto;
}
input{
    font-size: 100%;
}
/* common css */

/* responsive */
@media screen and (min-width:556px){
    .mainContent, .singlePost{
        grid-template-columns: repeat(2,1fr);
    }
}
@media screen and (min-width:884px){
    .mainContent{
        grid-template-columns: repeat(3,1fr);
    }
}
@media screen and (min-width:1240px){
    .container{
        width: 75% !important;
        margin: auto !important;
    }
    .section{
        padding: 5rem 0 3rem;
    }
    .secIntro{
        margin: 0 auto 3rem;
    }
    .secIntro,.secTitle{
        line-height: 2rem;
        padding-bottom: 1rem;
        font-size: 2.2rem;
    }
    .secIntro p{
        font-size: 15px;
    }
}
