/* page design */
::selection{
    background: var(--primaryclr);
    color: var(--whiteclr);
}
::-webkit-scrollbar{
    width: 10px;
    background:var(--greybgclr) ;
}
::-webkit-scrollbar-thumb{
    background: var(--primaryclr);
    border-radius: 10px;
    cursor: pointer;
}