:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
}

.blog .mainContainer {
  gap: 1rem;
}

.blog .mainContainer .singlePost {
  gap: 2rem;
}

.blog .mainContainer .singlePost .imgDiv {
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  border: 5px solid var(--greybgclr);
  -webkit-box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.406);
          box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.406);
  overflow: hidden;
}

.blog .mainContainer .singlePost .imgDiv:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: 3s ease;
  transition: 3s ease;
}

.blog .mainContainer .singlePost .imgDiv img {
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.blog .mainContainer .singlePost .postDetails h3 {
  color: var(--blackclr);
  font-weight: 600;
  padding-bottom: 10px;
}

.blog .mainContainer .singlePost .postDetails p {
  font-size: 13px;
  color: var(--textclr);
  font-weight: 500;
}

.blog .mainContainer .singlePost a {
  font-size: 15px;
  color: var(--primaryclr);
  font-weight: 600;
  padding-top: .5rem;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.blog .mainContainer .singlePost a:hover {
  color: var(--hoverclr);
  font-weight: 600;
}

.blog .mainContainer .singlePost a:hover .icon {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

@media screen and (min-width: 884px) {
  .blog .mainContainer {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
  }
}
