:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
}

.footer {
  background: var(--greybgclr);
  padding: 2rem 0;
}

.footer .secContainer {
  gap: 1.5rem;
  text-align: center;
}

.footer .secContainer .footerLogo {
  padding-bottom: .5rem;
}

.footer .secContainer .footerLogo .logo {
  color: var(--blackclr);
  font-weight: 500;
  cursor: pointer;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.footer .secContainer .footerLogo .logo .icon {
  font-size: 25px;
  color: var(--primaryclr);
  margin-right: .5rem;
}

.footer .secContainer .socials {
  gap: .5rem;
  margin: auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.footer .secContainer .socials .icon {
  font-size: 30px;
  background: var(--primaryclr);
  border-radius: 50%;
  padding: 8px;
  color: var(--whiteclr);
}

.footer .secContainer .socials .icon:hover {
  background: var(--blackclr);
}

.footer .secContainer .footerLinks {
  display: -ms-grid;
  display: grid;
  row-gap: .2rem;
}

.footer .secContainer .footerLinks .linkTitle {
  padding-bottom: .5rem;
  color: var(--blackclr);
  font-weight: 700;
}

.footer .secContainer .footerLinks li:hover {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

.footer .secContainer .footerLinks li:hover a {
  color: var(--primaryclr);
}

.footer .secContainer .footerLinks a, .footer .secContainer .footerLinks .phone, .footer .secContainer .footerLinks .email {
  color: var(--textclr);
  font-size: 13px;
  font-weight: 500;
}

@media screen and (min-width: 440px) {
  .secContainer {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    text-align: center;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
  }
  .secContainer .footerLinks {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}

@media screen and (min-width: 640px) {
  .secContainer {
    -ms-grid-columns: (1fr)[4];
        grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 900px) {
  .secContainer {
    padding: 2rem;
  }
}
