:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
  --box-shadow: 0 2px 8px 2px rgba(178,178,178,0.334);
}

.popular {
  padding-top: 15rem;
}

.popular .secContainer .secHeader {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  gap: 1rem;
}

.popular .secContainer .secHeader .textDiv .secTitle {
  color: var(--blackclr);
  line-height: 1.5rem;
  padding-bottom: .5rem;
}

.popular .secContainer .secHeader .textDiv p {
  font-size: 13px;
  color: var(--textclr);
  font-weight: 500;
}

.popular .secContainer .secHeader .iconsDiv {
  gap: 0.5rem;
}

.popular .secContainer .secHeader .iconsDiv .icon {
  padding: .5rem;
  background: var(--greybgclr);
  border-radius: 50%;
  font-size: 2.5rem;
}

.popular .secContainer .secHeader .iconsDiv .icon:hover {
  background: var(--blackclr);
  color: var(--whiteclr);
}

.popular .secContainer .secHeader .iconsDiv .leftIcon {
  background: var(--blackclr);
  color: var(--whiteclr);
}

.popular .secContainer .secHeader .iconsDiv .leftIcon:hover {
  background: var(--greybgclr);
  color: var(--blackclr);
}

.popular .secContainer .mainContent {
  padding: 2rem 0;
  gap: 1rem;
}

.popular .secContainer .mainContent .singleDestination {
  overflow: hidden;
  border-radius: 1rem;
  -webkit-box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
          box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}

.popular .secContainer .mainContent .singleDestination .destImage {
  height: 200px;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.popular .secContainer .mainContent .singleDestination .destImage img {
  height: 100%;
  width: 100%;
