     object-fit: cover;
}

.popular .secContainer .mainContent .singleDestination .destImage .overlayInfo {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  padding: 0;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.846)), to(rgba(0, 0, 0, 0.759)));
  background: linear-gradient(rgba(0, 0, 0, 0.846), rgba(0, 0, 0, 0.759));
  bottom: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
}

.popular .secContainer .mainContent .singleDestination .destImage .overlayInfo h3 {
  color: var(--whiteclr);
  font-weight: 600;
}

.popular .secContainer .mainContent .singleDestination .destImage .overlayInfo p {
  color: var(--whiteclr);
  opacity: .7;
  font-size: 13px;
}

.popular .secContainer .mainContent .singleDestination .destImage .overlayInfo .icon {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: .5rem;
  background: var(--primaryclr);
  font-size: 2.5rem;
  border-radius: 50%;
  color: var(--whiteclr);
  border: 1px solid transparent;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.popular .secContainer .mainContent .singleDestination .destImage .overlayInfo .icon:hover {
  background: var(--blackclr);
  border: 1px solid var(--whiteclr);
  color: var(--primaryclr);
}

.popular .secContainer .mainContent .singleDestination .destFooter {
  padding: 1rem;
}

.popular .secContainer .mainContent .singleDestination .destFooter .number {
  font-size: 2rem;
  font-weight: 700;
  color: var(--blackclr);
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.popular .secContainer .mainContent .singleDestination .destFooter .number::after {
  content: '';
