  font-size: 15px;
}

.popular .secContainer .mainContent .singleDestination .destFooter .destText .flex {
  font-size: 16px;
  font-weight: 600;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

.popular .secContainer .mainContent .singleDestination .destFooter .destText .flex .icon {
  color: var(--primaryclr);
}

.popular .secContainer .mainContent .singleDestination:hover .overlayInfo {
  overflow: visible;
  padding: 1rem 1.5rem;
  height: 100%;
}

@media screen and (min-width: 480px) {
  .popular {
    padding-top: 10rem;
  }
}

@media screen and (min-width: 556px) {
