:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
}

.navBarSection .header {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background: transparent;
  width: 100%;
  padding: 1rem;
  z-index: 1000;
}

.navBarSection .header .logoDiv .logo {
  color: var(--whiteclr);
  font-weight: 500;
  cursor: pointer;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.navBarSection .header .logoDiv .logo .icon {
  color: var(--primaryclr);
  font-size: 25px;
  margin-right: 10px;
}

@media screen and (max-width: 768px) {
  .navBarSection .header .navBar {
    background: var(--greybgclr);
    position: absolute;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 1rem;
    width: 100%;
    top: 0;
    left: -500%;
    -webkit-box-shadow: 0 2px 4px rgba(209, 209, 209, 0.904);
            box-shadow: 0 2px 4px rgba(209, 209, 209, 0.904);
    -webkit-transition: .5s ease;
    transition: .5s ease;
    z-index: 1000;
  }
  .navBarSection .header .navBar .navLists {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 100%;
    margin: auto;
  }
  .navBarSection .header .navBar .navLists .navItem {
    padding: 1rem 0;
  }
  .navBarSection .header .navBar .navLists .navItem .navLink {
    color: var(--textclr);
    font-size: .9rem;
    font-weight: 600;
  }
  .navBarSection .header .navBar .navLists .navItem .navLink:hover {
    color: var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .headerBtns {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .navBarSection .header .navBar .navLists .headerBtns .btn {
    margin-top: 1rem;
  }
  .navBarSection .header .navBar .navLists .headerBtns .btn a {
    font-weight: 600;
  }
  .navBarSection .header .navBar .navLists .headerBtns .btn a:hover {
    color: var(--whiteclr);
  }
  .navBarSection .header .navBar .navLists .headerBtns .loginBtn {
    background: none;
    border: 1px solid var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .headerBtns .loginBtn a {
    color: var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .headerBtns .loginBtn:hover {
    background: var(--hoverclr);
  }
  .navBarSection .header .navBar .navLists .headerBtns .loginBtn:hover a {
    color: var(--whiteclr);
  }
  .navBarSection .header .navBar .closeNavbar {
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    color: var(--primaryclr);
  }
  .navBarSection .header .navBar .closeNavbar:hover .icon {
    color: var(--hoverclr);
  }
  .navBarSection .header .activeNavbar {
    left: 0;
  }
}

.navBarSection .header .toggleNavbar .icon {
  font-size: 25px;
  color: var(--whiteclr);
}

.navBarSection .header .toggleNavbar .icon:hover {
  color: var(--primaryclr);
}

.navBarSection .activeHeader {
  background: var(--textclr);
  -webkit-box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
          box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}

/* responsive */
@media screen and (min-width: 769px) {
  .navBarSection .toggleNavbar, .navBarSection .closeNavbar {
    display: none;
  }
  .navBarSection .header {
    padding: 1.5rem 2rem;
  }
  .navBarSection .header .navBar .navLists {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
  }
  .navBarSection .header .navBar .navLists .navItem .navLink {
    color: var(--whiteclr);
    padding: 0.45rem;
    font-size: .9rem;
    font-weight: 400;
  }
  .navBarSection .header .navBar .navLists .navItem .navLink:hover {
    color: var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .btn {
    margin-left: 1rem;
  }
  .navBarSection .header .navBar .navLists .btn a {
    color: var(--whiteclr);
    font-weight: 500;
  }
  .navBarSection .header .navBar .navLists .loginBtn {
    background: none;
    border: 1px solid var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .loginBtn:hover {
    background: var(--primaryclr);
  }
  .navBarSection .header .navBar .navLists .loginBtn:hover a {
    color: var(--whiteclr);
  }
}

@media screen and (min-width: 960px) {
  .header .navBar .navLists .navItem {
    padding: 0 .7rem;
  }
}

@media screen and (min-width: 1024px) {
  .header {
    padding: 1rem 8.5rem !important;
  }
}
