:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}

.about{
    .secContainer{
        .title{
            color: var(--blackclr);
            line-height: 1.5rem;
            padding-bottom: 2rem;
            text-align: center;
            font-size: 1.5rem;

        }
        .mainContent{
            gap:1rem;
            .singleItem{
                text-align: center;

                img{
                    background: rgba(247,144,54,0.265);
                    width: 100px;
                    border-radius: 50%;
                    padding: .5rem;
                }
                h3{
                    font-weight: 700;
                    color: var(--blackclr);
                    padding: .5rem;
                }
                p{
                    font-size: 13px;
                    font-weight: 500;
                    padding-bottom: .5rem;
                    color: var(--textclr);
                }
            }
        }

        .videoCard{
            margin-top: 2rem;
            background: linear-gradient(to top, rgba(0,0,0,0.73),rgba(0,0,0,0.609)) ;

            .cardContext{
                gap: 2rem;
                padding: 2rem 1rem;

                .cardText{
                    h2{
                        color: var(--whiteclr);
                        font-weight: 600;
                        padding-bottom: 1rem;
                    }
                    p{
                        color: var(--whiteclr);
                        opacity: .7;
                        font-size: 12px;
                        font-weight: 400;
                        letter-spacing: 1px;
                    }
                }
            }
            .cardVideo{
                border: 2px solid var(--whiteclr);
                height: 200px;
                width: 100%;
                border-radius: 10px;
                overflow: hidden;

                video{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

// responsive
@media screen and (min-width:720px){
    .about{
        .secContainer{
            .videoCard{
                .cardContext{
                    gap: 1rem;
                    padding: 4rem 3rem;

                    .cardText{
                        h2{
                            font-size: 2rem;
                        }
                        p{
                            opacity: .7;
                            font-size: 15px;
                        }
                    }
                }
                .cardContext{
                    grid-template-columns: repeat(2,1fr) !important;
                }
            }
        }
    }
    
 }

 @media screen and (min-width:1024px){
   
    .about{
        .secContainer{
            .mainContent{
                padding: 2.5rem 0;
            }
        }
    }
 }
 