:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}

.navBarSection{
    .header{
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: transparent;
        width: 100%;
        padding: 1rem;
        z-index: 1000;

        .logoDiv{
            .logo{
                color: var(--whiteclr);
                font-weight: 500;
                cursor: pointer;
                align-items: center;
                justify-content: center;

                .icon{
                    color: var(--primaryclr);
                    font-size: 25px;
                    margin-right: 10px;
                }
            }
        }

                @media screen and (max-width: 768px){
                    .navBar{
                        background: var(--greybgclr) ;
                        position: absolute;
                        height: max-content;
                        padding: 1rem;
                        width: 100%;
                        top: 0;
                        left:-500%;
                        box-shadow: 0 2px 4px rgba(209,209,209,0.904);
                        transition: .5s ease;
                        z-index: 1000;

                        .navLists{
                            flex-direction: column;
                            justify-content: center;
                            width: 100%;
                            margin: auto;

                            .navItem{
                                padding: 1rem 0;

                                .navLink{
                                    color: var(--textclr);
                                    font-size: .9rem;
                                    font-weight: 600;
                                    &:hover{
                                        color: var(--primaryclr);
                                    }
                                }
                            }
                            .headerBtns{
                                flex-direction: column;
                                .btn{
                                    margin-top: 1rem;
                                    a{
                                        font-weight: 600;

                                        &:hover{
                                            color: var(--whiteclr);
                                        }
                                    }
                                }
                                .loginBtn{
                                    background: none;
                                    border: 1px solid var(--primaryclr);

                                    a{
                                        color: var(--primaryclr);
                                    }
                                    &:hover{
                                        background: var(--hoverclr);
                                        a{
                                            color: var(--whiteclr);
                                        }
                                    }
                                }
                            }
                        }
                        .closeNavbar{
                            position: absolute;
                            top: 1rem;
                            right: 1.5rem;
                            color: var(--primaryclr);
                            &:hover .icon{
                                color: var(--hoverclr);

                            }
                        }
                    }
                    .activeNavbar{
                        left: 0;
                    }
                }

                .toggleNavbar{
                    .icon{
                        font-size: 25px;
                        color: var(--whiteclr);
                        &:hover{
                            color: var(--primaryclr);
                        }
                    }
                }
             }
             .activeHeader{
                background: var(--textclr);
                box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45);
             }
    }


    
/* responsive */
@media screen and (min-width:769px){
   .navBarSection{
    .toggleNavbar, .closeNavbar{
        display: none;
    }
    .header{
        padding: 1.5rem 2rem;
        .navBar{
            .navLists{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: row;

                    .navItem{
                        .navLink{
                            color: var(--whiteclr);
                            padding: 0.45rem;
                            font-size: .9rem;
                            font-weight: 400;
                            &:hover{
                                color: var(--primaryclr);
                            }
                        }
                    }
                    .btn{
                        margin-left: 1rem;
                        a{
                            color: var(--whiteclr);
                            font-weight: 500;
                        }
                        
                    }
                    .loginBtn{
                        background:none ;
                        border: 1px solid var(--primaryclr);

                        &:hover{
                            background: var(--primaryclr);
                            a{
                                color: var(--whiteclr);
                            }
                        }
                    }
                    }
        }
    }
   }
}

@media screen and (min-width:960px){
    .header{
        .navBar{
            .navLists{
                .navItem{
                    padding: 0 .7rem;
                }
            }
        }
    }
 }

 @media screen and (min-width:1024px){
    .header{
        padding: 1rem 8.5rem !important;
    }
 }

 
