:root{
    --primaryclr: hsl(26,93%,50%);
    --hoverclr: hsl(26,90%,42%);
    --whiteclr: hsl(0,0%,100%);
    --blackclr: hsl(0,0%,18%);
    --textclr: hsl(240,1%,48%);
    --whitedeamclr: hsl(0,0%,93%);
    --greybgclr: hsl(0,0%,96%);
    --greytextclr: hsl(190,190,190);
    --inputclr: hsl(330,12%,97%);
}

.home{
    height: 80vh;
    background: linear-gradient(rgba(33,33,33,0.522),rgba(33,33,33,0.522)),url('../Assets/travel\ \(7\).jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    position: relative;

    .secContainer{
        .homeText{
            text-align: center;

            .title{
                color: var(--whiteclr);
                font-size: 2rem;
                font-weight: 700;
                line-height: 2.5rem;
            }
            .subTitle{
                color: var(--whiteclr);
                opacity: .9;
                font-size: 13px;
                font-weight: 300;
                padding: 1rem 0;
                max-width: 70%;
                text-align: center;
                margin: auto;
            }
            .btn{
                padding: .6rem 1.5rem;
            }
        }
        .homeCard{
            width: 80%;
            border-radius: 1rem;
            margin-bottom: 20px;
            padding: 2rem;
            background-color: var(--whiteclr);
            position: absolute;
            gap: 1rem;
            justify-content: center;
            bottom: -35%;
            transform: translate(-50%);
            left: 50%;
            box-shadow: 0 2px 8px 4px rgba(178,178,178,0.45);

            .locationDiv, .disDiv, .priceDiv{
                width: 100%;

                label{
                    display: block;
                    color: var(--textclr);
                    font-weight: 500;
                    font-size: 13px;
                    padding-bottom: .4rem;
                }
                input{
                    width: 100%;
                    background: var(--inputclr);
                    border-radius: 10px;
                    border: none;
                    padding: 10px;
                    font-size: 13px;
                    font-weight: 500;

                    &::placeholder{
                        font-size: 13px;
                        opacity: .5;
                        font-weight: 400;
                    }
                    &:focus{
                        outline: none;
                    }
                }
            }
            .btn{
                width: 100%;
                padding: .6rem 1.5rem;
                color: var(--whiteclr);
                font-weight: 600;
            }
        }
    }
}

/* responsive */
@media screen and (min-width:480px){
    .home{
        .secContainer{
            .homeCard{
                bottom: -100px;
                width: 90%;
                grid-template-columns: repeat(2,1fr);
                margin-bottom: 20px;
                .btn{
                    transform: translateY(15px);
                }
            }
        }
    }
}

@media screen and (min-width:556px){
    .home{
        .secContainer{
            .homeText{
                .title{
                    font-size: 2.5rem;
                }
                .subTitle{
                    font-size: 18px;
                }
               
            }
        }
    }
}

@media screen and (min-width:720px){
    .home{
        .secContainer{
           .homeCard{
            bottom: -10%;
            grid-template-columns: repeat(4,1fr);
           }
        }
    }
}

@media screen and (min-width:720px){
    .home{
        .secContainer{
           .homeText{
            padding: 1rem 10rem;

            .title{
                font-size: 3.5rem;
                margin: auto;
                line-height: 4rem;
            }
            .subTitle{
                width: 70%;
            }
           }
        }
    }
}

@media screen and (min-width:1024px){
    .home{
        .secContainer{
            .homeText{
                .subTitle{
                    font-size: 20px;
                }
            }
            .homeCard{
                width: 70%;
            }
        }
    }
}