:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
}

.offer .secContainer .mainContent {
  gap: 1rem;
}

.offer .secContainer .mainContent .singleOffer {
  border-radius: 1rem;
  overflow: hidden;
  padding: .5rem;
  -webkit-box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.189);
          box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.189);
}

.offer .secContainer .mainContent .singleOffer .icon {
  font-size: 20px;
  color: var(--textclr);
}

.offer .secContainer .mainContent .singleOffer small {
  font-weight: 500;
  color: var(--textclr);
}

.offer .secContainer .mainContent .singleOffer .destImage {
  position: relative;
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  overflow: hidden;
}

.offer .secContainer .mainContent .singleOffer .destImage img {
  height: 100%;
}

.offer .secContainer .mainContent .singleOffer .destImage .discount {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  background: var(--primaryclr);
  color: var(--whiteclr);
  border-radius: 3rem;
  font-size: 10px;
  font-weight: 500;
}

.offer .secContainer .mainContent .singleOffer .offerBody {
  padding: 1rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .price {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.offer .secContainer .mainContent .singleOffer .offerBody .price h4 {
  color: var(--blackclr);
  font-weight: 700;
  font-size: 1.1rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .price .status {
  padding: 5px 10px;
  color: var(--hoverclr);
  background: var(--greybgclr);
  font-size: 10px;
  font-weight: 600;
  border-radius: 3rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .amenities {
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 1rem 0;
  gap: .5rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .amenities .singleAmenity {
  padding: 5px 10px;
  border-radius: 5px;
  background: var(--greybgclr);
  gap: .5rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .btn {
  margin-top: 1rem;
  width: 100%;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: .6rem;
  color: var(--whiteclr);
  font-weight: 600;
  gap: .5rem;
}

.offer .secContainer .mainContent .singleOffer .offerBody .btn .icon {
  color: var(--whiteclr);
}

.offer .secContainer .mainContent .singleOffer:hover {
  -webkit-transform: translateY(-7px);
          transform: translateY(-7px);
}
