:root {
  --primaryclr: hsl(26,93%,50%);
  --hoverclr: hsl(26,90%,42%);
  --whiteclr: hsl(0,0%,100%);
  --blackclr: hsl(0,0%,18%);
  --textclr: hsl(240,1%,48%);
  --whitedeamclr: hsl(0,0%,93%);
  --greybgclr: hsl(0,0%,96%);
  --greytextclr: hsl(190,190,190);
  --inputclr: hsl(330,12%,97%);
}

.about .secContainer .title {
  color: var(--blackclr);
  line-height: 1.5rem;
  padding-bottom: 2rem;
  text-align: center;
  font-size: 1.5rem;
}

.about .secContainer .mainContent {
  gap: 1rem;
}

.about .secContainer .mainContent .singleItem {
  text-align: center;
}

.about .secContainer .mainContent .singleItem img {
  background: rgba(247, 144, 54, 0.265);
  width: 100px;
  border-radius: 50%;
  padding: .5rem;
}

.about .secContainer .mainContent .singleItem h3 {
  font-weight: 700;
  color: var(--blackclr);
  padding: .5rem;
}

.about .secContainer .mainContent .singleItem p {
  font-size: 13px;
  font-weight: 500;
  padding-bottom: .5rem;
  color: var(--textclr);
}

.about .secContainer .videoCard {
  margin-top: 2rem;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.73)), to(rgba(0, 0, 0, 0.609)));
  background: linear-gradient(to top, rgba(0, 0, 0, 0.73), rgba(0, 0, 0, 0.609));
}

.about .secContainer .videoCard .cardContext {
  gap: 2rem;
  padding: 2rem 1rem;
}

.about .secContainer .videoCard .cardContext .cardText h2 {
  color: var(--whiteclr);
  font-weight: 600;
  padding-bottom: 1rem;
}

.about .secContainer .videoCard .cardContext .cardText p {
  color: var(--whiteclr);
  opacity: .7;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 1px;
}

.about .secContainer .videoCard .cardVideo {
  border: 2px solid var(--whiteclr);
  height: 200px;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
}

.about .secContainer .videoCard .cardVideo video {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

@media screen and (min-width: 720px) {
  .about .secContainer .videoCard .cardContext {
    gap: 1rem;
    padding: 4rem 3rem;
  }
  .about .secContainer .videoCard .cardContext .cardText h2 {
    font-size: 2rem;
  }
  .about .secContainer .videoCard .cardContext .cardText p {
    opacity: .7;
    font-size: 15px;
  }
  .about .secContainer .videoCard .cardContext {
    -ms-grid-columns: (1fr)[2] !important;
        grid-template-columns: repeat(2, 1fr) !important;
  }
}

@media screen and (min-width: 1024px) {
  .about .secContainer .mainContent {
    padding: 2.5rem 0;
  }
}
